.content__row {
  display: flex;
}
.content__menu {
  flex-shrink: 0;
  max-width: 16.25rem;
  margin-right: 1.25rem;
  width: 100%;
}
.content__menu-body {
  position: sticky;
  top: 0.9375rem;
}
.content__right {
  width: 100%;
}
.content__title {
  font-size: 3.125rem;
  font-weight: 700;
  line-height: 3.125rem;
  color: #323333;
}
.content__title:not(:last-child) {
  margin-bottom: 2.5rem;
}
.content__report:not(:last-child) {
  margin-bottom: 3.75rem;
}